<template>
    <yd-layout>
        <div class="mainContentFluid peruse">
            <div class="fluidWidth">
                <div class="title" v-text="jsonData.docTitle">
                </div>
                <div class="articleInfo">
                    <div class="mess">
                        <div class="publisher">发布者：{{$route.params.dept}}</div>
                        <div class="padingLeft" v-text="current"></div>
                        <!-- <div class="padingLeft">阅读量：{{jsonData.docLevel}}</div> -->
                    </div>
                </div>
                <div class="articleTxt" v-html="jsonData.docContent">
                </div>
            </div>
        </div>
        <!-- <div class="attachment" v-if="jsonData.attachList.length > 0" v-for="item in jsonData.attachList" :key="item.attach_name">
            <div class="attLogo">
                <img src="../../../static/images/trs_yl_att.png">
            </div>
            <a href="http://www.baidu.com" v-bind:href="item.attach_url">
                <div class="attName">
                    <div class="name" v-text="item.attach_name"></div>
                    <yd-navbar-next-icon></yd-navbar-next-icon>
                </div>
            </a>
        </div> -->
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'detail',
    data() {
        return {
            jsonData: {},
            current: ''
        }
    },
    mounted() {
        if (this.$route.params.flag == 0) {
            document.setTitle('公司公告');
        } else {
            document.setTitle('银联要闻');
        }
        this.initData();
    },
    computed: {
        ...mapGetters([
            'getUserInfo'
        ])
    },
    methods: {
        initData() {
            var vm = this;
            let date = vm.$route.params.date;
            vm.getLocalTime(date);
            // console.log(vm.current);
            vm.get(vm.conf.portalMobile + '/API/AddressBook/affiche/detail', {
                name: vm.getUserInfo.ssotoken,
                docId: vm.$route.params.newsId
            }, function (res) {
                vm.jsonData = res.jsonData;
                vm.jsonData.docContent = vm.jsonData.docContent.replace(/\n/g, '<br>');
                if (vm.$route.params.flag == 0) {
                    vm.jsonData.docContent = [vm.trim(vm.jsonData.docContent.split('<br>')[1]), ...vm.jsonData.docContent.split('<br>').slice(2)].join('<br>');
                }
            });
        },
        getLocalTime(date) {
            let vm = this;
            vm.current = new Date(parseInt(date) * 1000).getFullYear() + '年' + (new Date(parseInt(date) * 1000).getMonth() + 1) + '月' + new Date(parseInt(date) * 1000).getDate() + '日';
        }
    }

}
</script>
<style lang="less" scoped>
.mess{
    overflow: hidden;
    div{
        float: left;
    }
}
.articleInfo{
    margin-bottom: .2rem;
}
.peruse {
    margin-top: 0;
}

.title {
    font-size: 0.3rem;
    margin-bottom: .2rem;
    font-weight: 600;
}

.articleInfo .yd-flexbox {
    line-height: 0.9rem;
    color: #999;
    font-size: 0.24rem;
}

.articleInfo .yd-flexbox-item {
    text-align: center;
}

.publisher {
    width: 2.1rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.padingLeft {
    padding-left: 0.1rem;
}

.articleTxt {
    font-size: 0.3rem;
    line-height: 0.5rem;
}

.attachment {
    width: 100%;
    height: 1rem;
    border-bottom: 1px solid #ccc;
    border-top: 1px solid #ccc;
    background-color: #fff;
}

.attLogo {
    float: left;
    width: 0.8rem;
    height: 0.8rem;
    background-color: #80B7F9;
    margin-left: 0.2rem;
    margin-top: 0.1rem;
    position: relative;
}

.attLogo img {
    width: 0.42rem;
    height: .42rem;
    position: absolute;
    top: 0.2rem;
    left: 0.2rem;
}

.attName {
    float: left;
    height: 1rem;
    width: 6rem;
    font-size: 0.26rem;
    line-height: 1rem;
    margin-left: 0.2rem;
}

.attName span {
    float: right;
}

.name {
    width: 5rem;
    overflow: hidden;
    float: left;
    height: 1rem;
    text-overflow: ellipsis;
    white-space: nowrap;
}
</style>


